  border: none;
}

/*Parent Menu Container (ul)*/

.floatingMenu {
  pointer-events: none;
  position: fixed;
  overflow: hidden;
  z-index: 98;
  bottom: 3.8rem;
  padding: 0.2em 0.4rem 2.4rem 0;
  right: 2rem;
  display: flex;
  flex-direction: column;
}

.floatingMenu.open {
  pointer-events: inherit;
}

/*Menu Items (li's)*/

.floatingMenu li {
  opacity: 0;
  top: 20em;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  -moz-column-gap: 1em;
       column-gap: 1em;
  margin-bottom: 1em;
  transition: top 0.5s ease, opacity 0.5s ease;
}

.floatingMenu li.visible {
  opacity: 1;
  top: 0;
}

.floatingMenu li:last-of-type {
  margin-bottom: 0;
}

/*Menu Item Labels (spans)*/

.floatingMenu li span {
  padding: 0.4rem;
  opacity: 0;
  font-size: 0.7rem;
  color: white;
  border-radius: 3px;
  background-color: #111111;
  transition: opacity 0.5s ease;
}

.floatingMenu li:hover span {
  opacity: 1;
}

/*Icons*/

.floatingMenu li a i {
  color: white;
  display: block;
  font-size: 1.375rem;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  text-align: center;
  line-height: 3rem;
  background-color: #222222;
  transition: transform 0.5s ease;
}

.floatingMenu li a:hover i {
  transform: scale(1.06);
}

